.banner-section {
  position: relative;
  width: 100%;
  background-color: red;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.banner-text {
  position: relative;
  font-size: 28rem;
  font-weight: bold;
  background-image: url("../../../assets/images/spark_light.png");
  background-size: cover;
  background-position: inherit;
  background-repeat: repeat;

  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;

  /* Optional shadow to enhance definition */
  text-shadow: 0 0 0 rgba(0, 0, 0, 0.3);
}

.banner-text::after {
  content: "";
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.3); /* Darken the image inside the text */
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  pointer-events: none;
}

.banner-text-wrapper {
  width: 100%;
  height: 1000px;
  background-color: red;
  overflow: hidden;
  display: flex;
  align-items: center;
  position: relative;
  justify-content: center;
}

.dice-left {
  position: absolute;
  left: -15%;
  bottom: -34%;
  width: 50%;
  height: min-content;
  animation: spin 25s linear infinite;
  transform-origin: center;
}

.dice-right {
  width: 15rem;
  position: absolute;
  top: 0;
  left: 18%;
}
